export default {
  data () {
    return {
      tabBarHeight: 50,
      weapons: [
        {
          id: 1, name: '普通子弹', image: 'https://img.alicdn.com/imgextra/i1/2200676927379/O1CN01uJCYub24Ndcu7OAfC_!!2200676927379.png', description: '基础伤害，适合新手',
          power: 1

        },
        {
          id: 2, name: '激光炮', image: 'https://img.alicdn.com/imgextra/i4/2200676927379/O1CN019Rzpoy24NdcsJhZQI_!!2200676927379.png', description: '高伤害，穿透效果',
          power: 10
        },
        {
          id: 3, name: '散弹炮', image: 'https://img.alicdn.com/imgextra/i2/2200676927379/O1CN01wmcGde24Ndcv9VQ82_!!2200676927379.png', description: '范围伤害，适合群攻',
          power: 3
        }
      ],
      myPrizes: [
        { name: 'iPhone 17Pro', image: 'https://img.alicdn.com/imgextra/i1/2200676927379/O1CN01yLJvSU24Ndcp9wgrf_!!2200676927379.png', time: '2025-05-20 15:30' },
        { name: '华为三折叠', image: 'https://img.alicdn.com/imgextra/i4/2200676927379/O1CN01XKUIVX24NdcrU32eC_!!2200676927379.png', time: '2025-05-19 10:20' }
      ],
      bulletRechargeShow: false,
      rechargeRulesShow: false,
      myPrizesShow: false,
      weaponSwitchShow: false,
      currentWeapon: 1,
      powerReferenceShow: false, // 火力值参考弹窗
      feedbackShow: false, // 投诉反馈弹窗
      feedbackContent: '', // 反馈内容
      // 哪种支付方式
      moneyType: '3',
      // 支付方式
      moneyTypeList: [

        {
          id: '3',
          icon: 'https://img.alicdn.com/imgextra/i2/2200676927379/O1CN01mfiDjv24NdXdrF5yS_!!2200676927379.png',
          name: '微信支付'
        },









      ],
      rewardData: {},
      rewardPopupVisible: false,
      is_epay: 0,
      site_title: '',

      // 主播概率
      testHitConfig: {
        enabled: true,           // 是否启用测试逻辑
        testUserId: 123,         // 指定测试员 ID
        hitRange: [3, 5],        // 命中区间：[起始发数, 结束发数]
        fireCount: 0,           // 当前发射次数（每次触发 +1）
        targetHit: null, // 目标必中发数
      },
      isPaying: false,
      loginVisible: false,
      home_bg: ''
    }
  },
  onLoad (options) {

    this.optionsData = options

    // 微信二维码undo
    if (options.scene) {
      let invite_code = decodeURIComponent(options.scene)
      let index = invite_code.indexOf('=') + 1
      if (index > 0) {
        invite_code = invite_code.slice(index, invite_code.length)
        uni.setStorageSync('invite_code', invite_code);
      } else {
        uni.setStorageSync('invite_code', invite_code);
      }
    }



  },
  computed: {
    currentWeaponImage () {
      const weapon = this.weapons.find(w => w.id === this.currentWeapon);
      return weapon ? weapon.image : this.weapons[0].image;
    },
    currentWeaponText () {
      switch (this.currentWeapon) {
        case 1:
          return 'https://img.alicdn.com/imgextra/i1/2200676927379/O1CN01uJCYub24Ndcu7OAfC_!!2200676927379.png';
        case 2:
          return 'https://img.alicdn.com/imgextra/i4/2200676927379/O1CN019Rzpoy24NdcsJhZQI_!!2200676927379.png';
        case 3:
          return 'https://img.alicdn.com/imgextra/i2/2200676927379/O1CN01wmcGde24Ndcv9VQ82_!!2200676927379.png';
        default:
          return 'https://img.alicdn.com/imgextra/i1/2200676927379/O1CN01uJCYub24Ndcu7OAfC_!!2200676927379.png';
      }
    }
  },
  methods: {
    /**
     * 测试命中控制器：level 为 1 的测试账号，第 N~M 发子弹必中
     * @returns {Boolean} 是否强制命中
     */
    handleTestHit () {
      const cfg = this.testHitConfig;
      if (!cfg || !cfg.enabled) return false;

      // 判断是否是测试账号
      if (!this.userInfo || this.userInfo.level !== 1) return false;

      // 计数 +1
      cfg.fireCount += 1;

      // 第一次进入范围时，随机选择一个必中发数
      if (cfg.targetHit === null && cfg.fireCount >= cfg.hitRange[0] && cfg.fireCount <= cfg.hitRange[1]) {
        const min = cfg.hitRange[0];
        const max = cfg.hitRange[1];
        cfg.targetHit = Math.floor(Math.random() * (max - min + 1)) + min;
      }

      const isInRange = cfg.fireCount >= cfg.hitRange[0] && cfg.fireCount <= cfg.hitRange[1];
      const isHit = isInRange && cfg.fireCount === cfg.targetHit;

      if (isHit) {
        // console.log(`[测试命中] 第 ${cfg.fireCount} 发，强制命中`);
      } else {
        // console.log(`[测试命中] 第 ${cfg.fireCount} 发，正常命中`);
      }

      // 如果超过 max，重置计数和目标发数，方便下一轮测试
      if (cfg.fireCount > cfg.hitRange[1]) {
        cfg.fireCount = 0;
        cfg.targetHit = null;
      }

      return isHit;
    },
    checkPayStatus () {
      const _this = this
      const order_info = uni.getStorageSync('order_info_recharge_coin')

      _this.checkTimer = setInterval(() => {
        if (!order_info) {
          _this.cancelCheckPayStatus()
          return
        }

        // 查询订单
        this.req({
          url: '/v1/pay/rechargePayStatus',
          data: {
            order_sn: order_info.order_sn
          },
          Loading: true,
          success: (res) => {
            console.log(res.data, '支付结果查询')
            switch (res.code) {
              case 200:
                _this.cancelCheckPayStatus()
                this.$store.dispatch('getUserInfo').then(res => { this.userInfo = res.data })
                this.$common.toast({ title: '支付成功', icon: 'success', duration: 1500 })
                this.bulletRechargeShow = false;

                break;
              case 203:
                _this.cancelCheckPayStatus()
                this.$common.toast({ title: '支付失败', icon: 'none', duration: 500 })
                break;
            }
          }
        })
      }, 2 * 1000)

      _this.cancelTimer = setTimeout(() => {
        _this.cancelCheckPayStatus();
      }, 60 * 1000)
    },
    cancelCheckPayStatus () {
      uni.removeStorageSync('order_info_recharge_coin')
      if (this.checkTimer) {
        clearTimeout(this.checkTimer)
      }

      if (this.cancelTimer) {
        clearInterval(this.cancelTimer)
      }
    },

    goMy () {
      this.$common.to({
        type: 3,
        url: '/pages/tabBar/my'
      })
    },
    // 显示火力值参考
    showPowerReference () {
      this.powerReferenceShow = true;
    },

    // 显示投诉反馈
    showFeedback () {
      this.feedbackShow = true;
    },
    // 提交反馈
    submitFeedback () {
      if (!this.feedbackContent.trim()) {
        uni.showToast({
          title: '请输入反馈内容',
          icon: 'none'
        });
        return;
      }

      // 这里添加提交反馈的接口调用
      this.req({
        url: '/v1/feedback/submit',
        data: {
          content: this.feedbackContent
        },
        success: res => {
          if (res.code == 200) {
            uni.showToast({
              title: '反馈提交成功',
              icon: 'success'
            });
            this.feedbackShow = false;
            this.feedbackContent = '';
          }
        }
      });
    },
    // 添加获取我的奖品列表方法
    getMyPrizes () {
      this.req({
        url: '/v1/game/myPrizes',
        success: res => {
          if (res.code === 200) {
            this.myPrizes = res.data;
          }
        }
      });
    },
    switchWeapon () {
      this.weaponSwitchShow = true;
    },
    showMyPrizes () {
      this.myPrizesShow = true;
    },
    reloadAmmo () {
      this.bulletRechargeShow = true;
    },
    showRechargeRules () {
      this.rechargeRulesShow = true;
    },
    selectWeapon (weapon) {
      this.currentWeapon = weapon.id;
      this.weaponSwitchShow = false;
      // 这里可以添加切换武器的具体逻辑
    },
    confirmRecharge (selectedRecharge) {
      if (!this.userInfo.id) {
        this.loginVisible = true
        return
      }
      this.isPaying = true;
      // 这里调用支付接口
      this.req({
        url: '/v1/user/rechargecoin/gopay',
        data: {
          id: selectedRecharge.id,
          pay_type: this.moneyType,
        },
        success: res => {









          const params = { ...res.data, is_epay: this.is_epay, returnUrl: `/pages/tabBar/home` }
          this.$common.orderPay(params).then(res1 => {
            if (res1 == 'success') {
              this.$common.toast({
                title: '支付成功',
                icon: 'success',
                duration: 1500,
                success: () => {
                  this.$store.dispatch('getUserInfo').then(res => {
                    this.cancelCheckPayStatus()
                    this.userInfo = res.data
                    this.bulletRechargeShow = false;
                    this.isPaying = false
                  })
                }
              })
            } else {
              this.cancelCheckPayStatus()
              this.isPaying = false; // 支付未成功的情况也要重置
            }
          }).catch(() => {
            // 捕获异常的兜底处理
            this.cancelCheckPayStatus();
            this.isPaying = false;
            this.$common.toast({ title: '支付异常', icon: 'none' });
          });
        },
        fail: () => {
          this.isPaying = false;
          this.$common.toast({ title: '下单失败', icon: 'none' });
        }
      });
    },
    // 定时器清理
    clearTimer (timerId) {
      if (timerId) clearInterval(timerId);
      return null;
    },
    /* ==================== 用户交互方法 ==================== */
    rotateLeft () {
      if (this.cannonAngle > -60) {
        this.cannonAngle -= 10;
        // 强制更新视图
        this.$forceUpdate();
      }
    },
    rotateRight () {
      if (this.cannonAngle < 60) {
        this.cannonAngle += 10;
        // 强制更新视图
        this.$forceUpdate();
      }
    },

    /* ==================== 网络请求方法 ==================== */
    getList ({
      num,
      size
    }) {
      let data = {
        page: num,
        per_page: size,
        type: 1,
      }
      this.req({
        url: '/v1/fish/list',
        data,
        Loading: true,
        success: res => {
          if (res.code == 200) {
            if (num == 1) {
              this.listData = []
            }
            this.listData = [...this.listData, ...res.data.data]
            this.initFish()
          }
        }
      })
    },
    getRechargeList () {
      this.req({
        url: '/v1/user/rechargecoin',
        success: res => {
          if (res.code == 200) {
            this.coinList = res.data.data
          }
        }
      })
    },
    /* ==================== 同步系统子弹 ==================== */
    syncBullets () {
      const cacheKey = this.siteBaseUrl + '_localCoin';
      const cache = uni.getStorageSync(cacheKey);

      const serverCoin = this.userInfo.coin;
      const serverTime = new Date(this.userInfo.coin_updated_at || Date.now()).getTime();

      if (cache && typeof cache === 'object' && cache.count !== undefined) {
        const cachedCoin = parseInt(cache.count);
        const cachedTime = parseInt(cache.updatedAt || 0);
        if (cachedTime >= serverTime) {
          // 本地更新更晚 → 同步给服务端
          this.localCoin = cachedCoin;

          this.req({
            url: '/v1/game/setBullet',
            data: {
              count: this.localCoin
            },
            Loading: false,
            success: result => {
              // console.log('客户端子弹同步至服务端成功');
            }
          });
        } else {
          // 服务端更新更晚 → 用服务端数据更新本地
          this.localCoin = serverCoin;
          uni.setStorageSync(cacheKey, {
            count: this.localCoin,
            updatedAt: serverTime
          });
        }
      } else {
        // 没有缓存 → 默认使用服务端
        this.localCoin = serverCoin;
        uni.setStorageSync(cacheKey, {
          count: this.localCoin,
          updatedAt: serverTime
        });
      }
    },

    // 捕获泡泡
    checkRealHit (fish) {
      if (fish._isCaughtTemp) return; // 防止重复调用

      fish._isCaughtTemp = true;

      this.req({
        url: '/v1/game/catchFish',
        data: { fish_id: fish.id },
        success: res => {
          if (res.code == 200) {
            this.rewardData = res.data;
            this.rewardPopupVisible = true;
            this.playSound('catch');
            this.showCatchAnimation(fish);
            // 这里调用恢复血量和移动的逻辑
            this.resetFish(fish);
            // 重置测试员发射次数
            this.testHitConfig.fireCount = 0
            uni.showToast({ title: '恭喜中奖！', icon: 'success', duration: 1500 });
          } else {
            fish._isCaughtTemp = false; // 失败重置
            uni.showToast({ title: res.msg || '捕获失败', icon: 'none' });
          }
        },
        fail: () => {
          fish._isCaughtTemp = false; // 网络失败重置
          uni.showToast({ title: '网络错误，请重试', icon: 'none' });
        }
      });
    },
    resetFish (fish) {
      const index = this.fishList.findIndex(f => f.id === fish.id);
      if (index !== -1) {
        const newFish = {
          ...fish,
          x: this.gameArea.width + 100, // 重置到屏幕右侧
          y: this.gameArea.minY + Math.random() * (this.gameArea.maxY - this.gameArea.minY),
          maxY: this.gameArea.maxY,
          currentHp: fish.originalHp,
          isCaught: false,
          isSelected: false,
          _isCaughtTemp: false
        };
        this.$set(this.fishList, index, newFish);
      }
    },
    getUrlCode () { //
      var url = location.search
      var theRequest = new Object()
      if (url.indexOf("?") != -1) {
        var str = url.substr(1)
        var strs = str.split("&")
        for (var i = 0; i < strs.length; i++) {
          theRequest[strs[i].split("=")[0]] = (strs[i].split("=")[1])
        }
      }
      return theRequest
    },
    getOpenid (code) { //获取opendi
      if (this.isLoggingIn || uni.getStorageSync('used_code') === code) return

      this.isLoggingIn = true
      uni.setStorageSync('used_code', code)
      this.req({
        url: '/v1/shop/wxLogin',
        data: {
          code,
          invite_code: uni.getStorageSync('invite_code')
        },
        success: res => {
          if (res.code == 200) {
            if (res.code == 200) {
              uni.setStorageSync(
                'token',
                `${res.data.token_type}${res.data.access_token}`
              )

              // this.$store.dispatch('getUserInfo').then(res => {
              //   if (res.code == 200) {
              //     this.userInfo = res.data
              //   }
              // })
              this.clearUrlParams()
              uni.switchTab({
                url: '/pages/tabBar/home'
              })

              if (this.is_home_pop) {
                // 根据配置是否弹窗
                this.controlPopup()
              }

              // 等待标题更新后再设置页面标题
              uni.setNavigationBarTitle({
                title: this.site_title
              });
              // setTimeout(() => {
              //   this.pageTo('wxH5')
              // }, 100)
            }
          }
          this.isLoggingIn = false
        },
        fail: () => {
          this.isLoggingIn = false
        }
      })
    },
    pageTo (wx) {
      this.$store.dispatch('getUserInfo').then(() => {
        let pages = getCurrentPages()

        console.log('this.optionsData', this.optionsData)
        let data = ''
        if (this.optionsData) {
          let query = this.optionsData
          if (this.optionsData.page == 'package/fuli/fuli-detail') {
            query.is_new = 1
          }
          data = query
        }
        /* 小程序码进入 */
        if (this.optionsData.scene) {
          let query = {}
          let arr = this.optionsData.scene.split('_')
          if (arr[0] == 1) {
            query.userId = arr[1]
            query.id = arr[2]
          }
          data = query
        }
        if (wx) {
          // 清理 URL 中的参数部分
          this.clearUrlParams();
          // uni.switchTab({
          //   url: '/pages/tabBar/home'
          // })
        }
        uni.redirectTo({
          url: `${this.optionsData.page}${this.$common.qs(this.optionsData)}`,
          fail: err => {
            console.log('跳转失败--', this.optionsData.page)
            uni.switchTab({
              url: `${this.optionsData.page}`
            })
          }
        })
      })
    },
    clearUrlParams () {
      // 获取当前 URL
      let url = window.location.href;
      // 找到 URL 中的参数部分
      let index = url.indexOf('?');
      if (index > -1) {
        // 移除参数部分
        url = url.substring(0, index);
      }
      // 使用 History API 替换当前 URL
      history.replaceState(null, '', url);
    },
  }
}
