const request = function (param, backpage, backtype) {
  uni.getNetworkType({
    success: function (res) {
      if (res.networkType == 'none') {




        uni.showToast({
          title: '网络连接异常，请检查网络',
          icon: 'none'
        })

        return
      }
    }
  })
  var url = param.url,
    method = 'POST',
    header = {},
    data = param.data || {},
    file = param.file || '',
    files = param.files || '',
    Loading = param.Loading || false,
    name = param.fileName || 'file'

  var token = uni.getStorageSync('token')
  // var token='b8e5376dd5d738d86126289468a3af19948ffe65'
  // var token_type = 'bearer'
  // var access_token = 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpc3MiOiJodHRwczpcL1wvdGVzdC5taC50ZXN0LmxhaWJpbndsLmNuXC9hcGlcL3YxXC9zaG9wXC93eExvZ2luIiwiaWF0IjoxNjk1MjgwODYzLCJuYmYiOjE2OTUyODA4NjMsImp0aSI6InR6NzZ0NjVxYmZieTM0ckEiLCJzdWIiOjEwMiwicHJ2IjoiMjcyNWExZTQ0ZTQyZGMzODBlZGZmNGZmMGM1MzcwZjZhZGFhNzA0NCJ9.wDRCxHYbAfp5bk6K45sajDdQ-G-Gz51SDyZUmRe_1Aw'
  // var token = `${token_type}${access_token}`
  //拼接完整请求地址
  var requestUrl = Vue.prototype.siteBaseUrl + url
  //固定参数:仅仅在小程序绑定页面通过code获取token的接口默认传递了参数token = login
  if (!header.token) {
    //其他业务接口传递过来的参数中无token
    //uni.getStorageSync('token'); 参数中无token时在本地缓存中获取
  }

  //请求方式:GET或POST(POST需配置header: {'content-type' : "application/x-www-form-urlencoded"},)
  if (method) {
    method = method.toUpperCase() //小写改为大写
    if (method == 'POST') {
      if (file) {
        header = {
          Authorization: token
        }
      } else if (files) {
        header = {
          Authorization: token
        }
      } else {
        header = {
          // 'content-type': 'application/x-www-form-urlencoded',
          'content-type': 'application/json',
          Authorization: token
        }
      }
    } else {
      header = {
        'content-type': 'application/json'
      }
    }
  } else {
    method = 'GET'
    header = {
      'content-type': 'application/json'
    }
  }

  /* 添加公共请求头 */
  {
    let randomString = function (e) {
      e = e || 32
      var t = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678',
        a = t.length,
        n = ''
      for (var i = 0; i < e; i++) n += t.charAt(Math.floor(Math.random() * a))
      return n
    }

    var token = '11fds45452432FED5FS4D'
    var t = `${Math.floor(Date.now() / 1000)}`
    var r = `${randomString(16)}`

    var arr = [token, t, r]
    // console.log('arr', arr)

    arr.sort()
    // console.log('arr1', arr)
    // console.log(Date(t * 1000))

    var s = MD5(SHA1(arr.join(''))).toUpperCase()

    // console.log('t', t)
    // console.log('r', r)
    // console.log('sha1', SHA1(token + t + r))
    // console.log('md5', MD5(SHA1(token + t + r)))
    // console.log('s', s)
    header.t = t
    header.r = r
    header.s = s
  }

  //用户交互:加载圈
  if (!Loading) {
    // uni.showLoading({
    //   title: '加载中...'
    // })
  }
  //网络请求
  if (file) {
    uni.uploadFile({
      url: requestUrl,
      header: header,
      name: name,
      formData: data,
      filePath: file,
      success: res => {
        res.data = JSON.parse(res.data)
        if (!Loading) {
          uni.hideLoading()
        }
        if (res.statusCode && res.statusCode != 200) {
          //api错误
          // uni.showModal({
          // 	content: "" + res.errMsg
          // });
          // return;
        }
        if (res.data.code == 200) {
          //返回结果码code判断:1成功,-1错误
        } else if (res.data.code == 1) {
          //成功的提示




          uni.showToast({
            title: res.data.msg,
            icon: 'none'
          })

        } else if (res.data.code == -3) {
          //去登录
          uni.navigateTo({
            url: '/pages/personal/login/index'
          })
        } else {




          uni.showToast({
            title: res.data.msg,
            icon: 'none'
          })

          return
        }
        typeof param.success == 'function' && param.success(res.data)
      },
      fail: e => {
        console.log('网络请求fail:' + JSON.stringify(e))
        uni.showModal({
          content: '' + e.errMsg
        })
        typeof param.fail == 'function' && param.fail(e.data)
      },
      complete: () => {
        if (!Loading) {
          uni.hideLoading()
        }
        typeof param.complete == 'function' && param.complete()
        return
      }
    })
  } else if (files) {
    uni.uploadFile({
      url: requestUrl,
      header: header,
      formData: data,
      files: files,
      success: res => {
        res.data = JSON.parse(res.data)
        if (!Loading) {
          uni.hideLoading()
        }
        if (res.data.code == 2) {
          //返回结果码code判断:1成功,-1错误
        } else if (res.data.code == 1) {
          //成功的提示




          uni.showToast({
            title: res.data.msg,
            icon: 'none'
          })

        } else if (res.data.code == -3) {
          //去登录
          uni.navigateTo({
            url: '/pages/personal/login/index'
          })
        } else {




          uni.showToast({
            title: res.data.msg,
            icon: 'none'
          })

          return
        }
        typeof param.success == 'function' && param.success(res.data)
      },
      fail: e => {
        console.log('网络请求fail:' + JSON.stringify(e))
        uni.showModal({
          content: '' + e.errMsg
        })
        typeof param.fail == 'function' && param.fail(e.data)
      },
      complete: () => {
        if (!Loading) {
          uni.hideLoading()
        }
        typeof param.complete == 'function' && param.complete()
        return
      }
    })
  } else {
    uni.request({
      url: requestUrl,
      method: method,
      header: header,
      data: data,
      success: res => {
        if (!Loading) {
          uni.hideLoading()
        }
        /* 过期时替换新token */
        if (res.header.authorization) {
          // console.log('`````````````````````````', res.header.authorization)
          uni.setStorageSync('token', res.header.authorization)
        }
        if (res.statusCode && res.statusCode == 500) {
          console.log(res)
          setTimeout(() => {
            uni.showToast({
              title: '系统出错',
              icon: 'none'
            })
          }, 100)
        }
        if (res.data.code == 200) {
          //返回结果码code判断:200成功
        } else if (res.data.code == 400) {
          // 失败




          setTimeout(() => {
            uni.showToast({
              title: res.data.msg,
              icon: 'none'
            })
          }, 100)

        } else if (res.data.code == 1000) {
          // 签名错误不抛错
        } else if (res.data.code == 1001) {










        } else if (res.data.code == 1002) {
          // 需要登录




          // let pages = getCurrentPages()
          // Vue.prototype.$common.to({
          //   type: 2,
          //   url: '/pages/mine/login',
          //   query: Vue.prototype.$common.getOptions({
          //     page: `/${pages[pages.length - 1].route}`
          //   })
          // })

















          // let wxpages = getCurrentPages()
          // Vue.prototype.$common.to({
          //   type: 2,
          //   url: '/pages/mine/login',
          //   query: Vue.prototype.$common.getOptions({
          //     page: `/${wxpages[wxpages.length - 1].route}`
          //   })
          // })

        } else {




          // setTimeout(() => {
          //   uni.showToast({
          //     title: res.data.msg,
          //     icon: 'none'
          //   })
          // }, 100)

        }
        typeof param.success == 'function' && param.success(res.data)
      },
      fail: e => {
        console.log('网络请求fail:' + JSON.stringify(e))
        // uni.showModal({
        // 	content: "" + e.errMsg
        // });
        typeof param.fail == 'function' && param.fail(e.data)
      },
      complete: () => {
        if (!Loading) {
          uni.hideLoading()
        }
        typeof param.complete == 'function' && param.complete()
        return
      }
    })
  }
}

export default request
